@-webkit-keyframes fade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fade {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes changeColors {
  0% {
    color: red;
  }
  75% {
    color: green;
  }
  100% {
    color: red;
  }
}
@keyframes changeColors {
  0% {
    color: red;
  }
  75% {
    color: green;
  }
  100% {
    color: red;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@font-face {
  font-family: "Impact";
  src: local("Impact"), url("./styles/Impact.ttf") format("truetype");
}
.App {
  background-color: #000000;
  color: #f5f5f5;
  min-width: 100vw;
  max-width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.popup-window {
  position: fixed;
  max-width: 100%;
  top: 5.5rem;
  background-color: #f04c4c;
  color: #f5f5f5;
  font-size: 1.2rem;
  font-weight: 600;
  text-align: center;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  -webkit-animation: fade 4s;
          animation: fade 4s;
  z-index: 2;
}

.name {
  font-size: 0.8rem;
  padding: 0.5rem 0.1rem;
  margin-bottom: 1rem;
}

@media only screen and (min-width: 320px) and (max-width: 648px) {
  .name {
    padding: 0.1rem 0.1rem;
    margin-bottom: 0.2rem;
  }

  .popup-window {
    top: 0.5rem;
